import React from 'react';
import * as styles from "./Section03.scss";
import classNames from 'classnames';

const cx = classNames.bind(styles);

class Section03 extends React.Component {
    render() {
        return (
            <div className={cx(styles['third-section'], 'section')}>
                <div className={styles['third-section--desc']}>
                    <div className={styles['third-section--desc--integration']}>
                        <h2>APP INTEGRATION</h2>
                    </div>
                    <h1> <br/>  !</h1>
                    <p>    <br/>
                          ,  .</p>
                </div>
                <div className={styles['third-section--progress']}>
                    <div>
                        <img className={styles['third-section--progress--device']}
                             src={require('./web-bottle-3-icon-1@2x .png')} alt={'  '}/>
                        <span></span>
                    </div>
                    <div>
                        <img className={styles['third-section--progress--data']}
                             src={require('./web-bottle-3-date@2x.png')} alt={'  '}/>
                    </div>
                    <div>
                        <img className={styles['third-section--progress--device']}
                             src={require('./web-bottle-3-icon-2@2x .png')} alt={'  '}/>
                        <span>/</span>
                    </div>
                    <div>
                        <img className={styles['third-section--progress--data']}
                             src={require('./web-bottle-3-platform@2x.png')} alt={'  '}/>
                    </div>
                    <div>
                        <img className={styles['third-section--progress--device']}
                             src={require('./web-bottle-3-icon-3@2x.png')} alt={'  '}/>
                        <span></span>
                    </div>
                    <span className={styles['horizon-bar']}></span>
                </div>
                <div className={styles['third-section--devices']}>
                    <img src={require('./web-bottle-3-bottle@2x.png')} alt={' '}/>
                    <img src={require('./web-bottle-3-mobile@2x.png')} alt={'  '}/>
                </div>


            </div>
        )
    }
}

export default Section03