import React from 'react';
import * as styles from './Section04.scss';


class Section04 extends React.Component {
    render() {
        return(
            <section className={styles['fourth-section']}>
                <div className={styles['fourth-section--article']}>
                    <div>
                        <h1>Blissful Innovation<br/><em> Introducing our line of smart devices.</em></h1>
                        <p>Use Littleones smart series to monitor and provide care for your<br/> baby anywhere and anytime. Safety and well-being of a child<br/> translates to parents joy.</p>
                    </div>
                </div>
                <div className={styles['fourth-section--mobile']}>
                    <div className={styles['fourth-section--mobile--devices']}>   .</div>
                    <div className={styles['fourth-section--mobile--mobile']}>   .</div>
                    <div className={styles['fourth-section--mobile--pictures']}>    .</div>
                </div>
            </section>
            )

    }
}

export default Section04;
